header {
  background-color: #7fffd4;
  flex-direction: column;
  align-items: center;
  padding: 20px 100px;
  display: flex;
}

.search-form {
  background-color: #fff;
  border-radius: 7px;
  justify-content: center;
  align-items: center;
  max-width: 500px;
  margin: 20px auto;
  display: flex;
  box-shadow: 3px 3px 10px 5px #c3bdf5, inset 0 0 4px rgba(0, 0, 0, .5);
}

.search-form input[type="text"] {
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 18px;
  transition: all .2s ease-in-out;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, .2);
}

.gallery {
  flex-flow: wrap;
  justify-content: center;
  gap: 20px;
  margin-left: 20px;
  margin-right: 20px;
  padding-top: 60px;
  display: flex;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  align-items: center;
  max-width: 300px;
  box-shadow: 3px 3px 10px 5px #c3bdf5, inset 0 0 4px rgba(0, 0, 0, .5);
}

.photo-card img {
  object-fit: cover;
  cursor: pointer;
  border-radius: 5px;
  width: 100%;
  height: 300px;
  transition: transform .2s ease-in-out;
}

.photo-card img:hover {
  transform: scale(1.05);
}

.info {
  flex-direction: row;
  gap: 8px;
  padding: 10px;
  display: flex;
}

.info-item {
  text-align: center;
  padding-top: 4px;
}

.search-form button[type="submit"] {
  color: #fff;
  background-color: #aba3f3;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: bold;
  transition: all .3s ease-in-out;
  display: inline-block;
  box-shadow: 0 0 10px rgba(0, 0, 0, .3);
}

.search-form button[type="submit"]:hover {
  transform: scale(1.07);
}

.load-more {
  color: #fff;
  background-color: #aba3f3;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: bold;
  transition: all .3s ease-in-out;
  display: block;
  box-shadow: 0 0 10px rgba(0, 0, 0, .3);
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

